
body{
   margin: 0 0 0 0;
}
.preheader{
   margin: 0%;
   padding: 0%;
   width: 100%;
   height: 1 vh;
   min-height: 20px;
   background-color: #ceb10f ;
}
.header{
   margin: 0%;
   padding: 0%;
   width: 100%;
   height: 10vh;
   min-height: 100px;
   background-color: #ffffff ;
   color:#000000 ;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
}
.header h1{
 
   width: 100%;
   text-align: center;
   font: Bold 150% Tahoma;
}
.header nav {
   height: 100%;
   width: 45%;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: distribute;
       justify-content: space-around;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   font-size: 30px;
}
.header nav button{
   
   color: #000000;
   background: #ffffff;
   border: 1px solid #9b9ea0;
   border-radius: 5px;
   padding: 10px 20px;
   cursor: pointer;
   font:  60% Tahoma;
}
.header nav button:hover{
   color: #ffffff;
   background: #4e5862;
   border: 1px solid #181f27;
}
.main-component{
   height: 75vh;
   margin: 0%;
   padding: 0%;
   background: #ffffff ;
   -ms-flex-line-pack:center;
       align-content:center;
}
.footer{
   margin: 0;
   padding: 0;
   width: 100%;
   height: 10vh;
   min-height: 100px;
   background-color: #000000;
   color: #E5E7E9;
}
.footer h2{
   width: 100%;
   height: 100%;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: center;
       -ms-flex-pack: center;
           justify-content: center;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
}




